'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Loader2, Mail, Lock, AlertCircle } from 'lucide-react'
import { supabase } from '@/lib/supabase'

// Force dynamic rendering to avoid AuthContext issues during build
export const dynamic = 'force-dynamic'

export default function LoginPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)
  const router = useRouter()

  // Debug functions for troubleshooting
  useEffect(() => {
    console.log('üîê Login page: Component mounted')

    // Add debug functions to window
    ;(window as any).getUserId = async () => {
      console.log('üë§ Getting current user ID...')
      if (!supabase) {
        console.error('üë§ No Supabase client')
        return null
      }

      try {
        console.log('üë§ Calling supabase.auth.getUser()...')

        // Add timeout to prevent hanging
        const timeoutPromise = new Promise((_, reject) =>
          setTimeout(() => reject(new Error('Timeout: getUser took too long')), 5000)
        )

        const getUserPromise = supabase.auth.getUser()
        const { data, error } = await Promise.race([getUserPromise, timeoutPromise]) as any

        console.log('üë§ getUser result:', {
          hasData: !!data,
          hasUser: !!data?.user,
          userId: data?.user?.id,
          hasError: !!error,
          errorMessage: error?.message
        })

        if (error) {
          console.error('üë§ Error getting user:', error)
          return null
        }

        const userId = data?.user?.id
        console.log('üë§ Current user ID:', userId)

        if (userId) {
          console.log('üë§ Usage: checkProfile("' + userId + '") or createProfile("' + userId + '")')
        } else {
          console.log('üë§ No user ID found - you may not be logged in yet')
          console.log('üë§ Try logging in first, then run this function again')
        }

        return userId
      } catch (err) {
        console.error('üë§ Failed to get user ID:', err)
        return null
      }
    }

    ;(window as any).debugLogin = async () => {
      console.log('üîê Debug: Checking Supabase client...')
      console.log('üîê Debug: Supabase available:', !!supabase)

      if (supabase) {
        console.log('üîê Debug: Testing getSession...')
        try {
          const { data, error } = await supabase.auth.getSession()
          console.log('üîê Debug: Current session:', {
            hasSession: !!data.session,
            hasUser: !!data.session?.user,
            sessionError: error?.message,
            userEmail: data.session?.user?.email
          })
        } catch (err) {
          console.error('üîê Debug: Session check failed:', err)
        }
      }
    }

    ;(window as any).testLogin = async (email: string, password: string) => {
      console.log('üîê Debug: Testing login with:', email)
      if (!supabase) {
        console.error('üîê Debug: No Supabase client')
        return
      }

      try {
        const { data, error } = await supabase.auth.signInWithPassword({
          email,
          password,
        })

        console.log('üîê Debug: Login result:', {
          hasError: !!error,
          errorMessage: error?.message,
          hasUser: !!data?.user,
          hasSession: !!data?.session,
          sessionExpiry: data?.session?.expires_at
        })

        return { data, error }
      } catch (err) {
        console.error('üîê Debug: Login test failed:', err)
        return { error: err }
      }
    }

    // Emergency login bypass for testing
    ;(window as any).forceRedirect = () => {
      console.log('üö® Force redirecting to dashboard...')
      window.location.href = '/dashboard'
    }

    ;(window as any).emergencyLogin = async (email: string, password: string) => {
      console.log('üö® Emergency login: Bypassing normal flow for:', email)

      try {
        // Clear any existing session first
        await supabase?.auth.signOut()

        // Try login with minimal configuration
        const { data, error } = await supabase.auth.signInWithPassword({
          email,
          password,
        })

        if (error) {
          console.error('üö® Emergency login failed:', error)
          return { success: false, error: error.message }
        }

        console.log('üö® Emergency login successful, forcing redirect...')
        // Force redirect immediately
        window.location.href = '/dashboard'
        return { success: true, data }
      } catch (err) {
        console.error('üö® Emergency login error:', err)
        return { success: false, error: err.message }
      }
    }

    // Auto-run debug on page load
    ;(window as any).debugLogin()

    console.log('üîê Login page: Debug functions available:')
    console.log('üîê - debugLogin(): Check current session')
    console.log('üîê - getUserId(): Get current user ID for profile debugging')
    console.log('üîê - testLogin(email, password): Test login directly')
    console.log('üîê - emergencyLogin(email, password): Bypass all middleware')
    console.log('üîê - forceRedirect(): Force redirect to dashboard')
    console.log('üîê - checkProfile(userId): Check user profile status')
    console.log('üîê - createProfile(userId): Force create user profile')

    // Profile debugging functions
    ;(window as any).checkProfile = async (userId: string) => {
      console.log('üë§ Checking profile for user:', userId)

      if (!supabase) {
        console.error('üë§ No Supabase client')
        return
      }

      try {
        const { data, error } = await supabase
          .from('user_profiles')
          .select('*')
          .eq('id', userId)
          .single()

        console.log('üë§ Profile check result:', {
          hasData: !!data,
          hasError: !!error,
          errorCode: error?.code,
          errorMessage: error?.message,
          profileData: data
        })

        return { data, error }
      } catch (err) {
        console.error('üë§ Profile check failed:', err)
        return { error: err }
      }
    }

    ;(window as any).createProfile = async (userId: string) => {
      console.log('üë§ Creating profile for user:', userId)

      if (!supabase) {
        console.error('üë§ No Supabase client')
        return
      }

      try {
        // First get current user data
        const { data: userData } = await supabase.auth.getUser()

        if (!userData?.user) {
          console.error('üë§ No user data available')
          return { error: 'No user data' }
        }

        console.log('üë§ Creating profile with data:', {
          id: userId,
          email: userData.user.email,
          name: userData.user.user_metadata?.name
        })

        const { data, error } = await supabase
          .from('user_profiles')
          .insert({
            id: userId,
            email: userData.user.email!,
            full_name: userData.user.user_metadata?.name || null,
            role: 'transcriptionist'
          })
          .select()
          .single()

        console.log('üë§ Profile creation result:', {
          hasData: !!data,
          hasError: !!error,
          errorMessage: error?.message,
          profileData: data
        })

        return { data, error }
      } catch (err) {
        console.error('üë§ Profile creation failed:', err)
        return { error: err }
      }
    }

    // Test if AuthManager is causing issues
    ;(window as any).testWithoutAuthManager = async (email: string, password: string) => {
      console.log('üîß Testing login without AuthManager interference...')

      try {
        // Temporarily disable AuthManager if it exists
        const authManagerModule = await import('@/lib/auth-utils')
        if (authManagerModule.authManager) {
          console.log('üîß Disabling AuthManager temporarily...')
          // This will prevent AuthManager from interfering
          authManagerModule.authManager.clearCache()
        }

        const { data, error } = await supabase.auth.signInWithPassword({
          email,
          password,
        })

        console.log('üîß Direct login result:', {
          hasError: !!error,
          errorMessage: error?.message,
          hasUser: !!data?.user,
          hasSession: !!data?.session
        })

        if (!error) {
          console.log('üîß Direct login successful - AuthContext will handle redirect')
        }

        return { data, error }
      } catch (err) {
        console.error('üîß Direct login failed:', err)
        return { error: err }
      }
    }
  }, [router])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setLoading(true)

    console.log('üîê Login: Starting login process for:', email)

    if (!supabase) {
      const errorMsg = 'Supabase client not available'
      console.error('üîê Login:', errorMsg)
      setError(errorMsg)
      setLoading(false)
      return
    }

    try {
      console.log('üîê Login: Calling signInWithPassword...')
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      })

      console.log('üîê Login: Supabase response:', {
        hasError: !!error,
        errorMessage: error?.message,
        hasUser: !!data?.user,
        hasSession: !!data?.session,
        sessionExpiry: data?.session?.expires_at
      })

      if (error) {
        console.error('üîê Login: Authentication error:', error)
        setError(error.message)
        setLoading(false)
      } else {
        console.log('üîê Login: Authentication successful!')

        // Reset loading state - AuthContext will handle redirect automatically
        setLoading(false)

        console.log('üîê Login: AuthContext will handle automatic redirect to dashboard...')
      }
    } catch (err: any) {
      console.error('üîê Login: Unexpected error:', err)
      setError(err.message || 'Login failed')
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900 px-4">
      <Card className="w-full max-w-md">
        <CardHeader className="space-y-1">
          <CardTitle className="text-2xl font-bold text-center">Welcome back</CardTitle>
          <CardDescription className="text-center">
            Enter your credentials to access your dashboard
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            {error && (
              <div className="bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 p-3 rounded-lg flex items-start gap-2">
                <AlertCircle className="h-5 w-5 mt-0.5 flex-shrink-0" />
                <span className="text-sm">{error}</span>
              </div>
            )}

            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <div className="relative">
                <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                <Input
                  id="email"
                  type="email"
                  placeholder="you@example.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="pl-10"
                  required
                  disabled={loading}
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <div className="relative">
                <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                <Input
                  id="password"
                  type="password"
                  placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="pl-10"
                  required
                  disabled={loading}
                />
              </div>
            </div>

            <div className="flex items-center justify-between">
              <Link
                href="/forgot-password"
                className="text-sm text-primary hover:underline"
              >
                Forgot password?
              </Link>
            </div>

            <Button type="submit" className="w-full" disabled={loading}>
              {loading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Signing in...
                </>
              ) : (
                'Sign in'
              )}
            </Button>

            <div className="text-center text-sm">
              Don&apos;t have an account?{' '}
              <Link href="/signup" className="text-primary hover:underline">
                Sign up
              </Link>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
